    class="Select"
  >
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value"
    />
  </el-select>
  <div class="aa">
    <input
      type="text"
      class="LogUserNumberInput"
      v-model="logUserNumber"
      placeholder="请根据查询依据输入"
    />
    <button>查询</button>
    <button>编辑</button>
  </div> -->

  <div class="StudentGradeBackground">
    <table>
      <thead>
        <tr>
          <th style="width: 2%">学号</th>
          <th style="width: 2%">姓名</th>
          <th style="width: 2%">专业</th>
          <th style="width: 2%">年级班级</th>
          <th style="width: 1.5%">课程名</th>
          <th style="width: 1%">课程学分</th>
          <th style="width: 1%">课程模块</th>
          <th style="width: 3%">开课学期</th>
          <th style="width: 1%">期末成绩</th>
          <th style="width: 1%">是否通识</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="records in StudentGradeTable" :key="records.studentNumber">
          <td>{{ records.studentNumber }}</td>
          <td>{{ records.studentName }}</td>
          <td>{{ records.major }}</td>
          <td>{{ records.grade }}{{ records.classes }}</td>
          <td>{{ records.studentName }}</td>
          <td>{{ records.courseCredit }}</td>
          <td>{{ records.courseModule }}</td>
          <td>{{ records.courseStartDateTime }}</td>
          <td>{{ records.courseScore }}</td>
          <td>{{ records.studyMethod }}</td>
        </tr>
      </tbody>
    </table>
  </div>
  <div class="StudentGradePagination">
    <el-pagination
      size="small"
      :page-size="pageSize"
      :pager-count="10"
      background
      layout="prev, pager, next"
      :total="total"
      @current-change="handlePageChange"
    />
  </div>
</template>

<script>
import axios from "@/axios";
export default {
  data() {
    return {
      StudentGradeTable: [],
      currentPage: 1,
      pageSize: 15,
      total: 130,
      /*       title: "",
      radio: "公开",
      value: "",
      selectedFile: null,

      options: [
        {
          value: "学号",
          label: "学号",
        },
        {
          value: "Python",
          label: "课程编号",
        },
        {
          value: "C语言",
          label: "课程学分",
        },
        {
          value: "Java",
          label: "学生分数（该分数往上）",
        },
        {
          value: "算法",
          label: "学生分数（该分数往下）",
        },
      ], */
    };
  },

  mounted() {
    this.FetchData();
  },
  methods: {
    handlePageChange(page) {
      this.currentPage = page;
      this.FetchData();
    },

    async FetchData() {
      try {
        const informationResponse = await axios.get("/admin/getStudentsGrade", {
          params: {
            page: this.currentPage,
            pageSize: this.pageSize,
          },
        });
        if (informationResponse.code == 1) {
          this.total = informationResponse.data.total;
          this.StudentGradeTable = informationResponse.data.records;
        } else {
          this.$message("数据加载失败!");
        }
      } catch (error) {
        this.$message("数据加载失败!");
        console.error("报错原因：", error);
      }
    },
  },
};
</script>

<style>
.StudentGradePagination {
  position: absolute;
  top: 94%;
}
.StudentGradeBackground {
  position: relative;
  overflow: auto;
  width: 100%;
  height: 95%;
}

@import "/src/components/ManageSystem/TableStyle.css";
</style>
